.page{position:absolute;top:0;left:0;width:100vw;height:95vh;border-right:2px solid #000000;overflow-y:scroll}.template{display:none}#pageContainer{margin-top:1em;position:relative}#loadingOverlay{position:absolute;top:0;left:0;width:100vw;height:100vh;background-color:#000000;z-index:200;display:none}#loadingOverlay img{position:absolute;top:50%;left:50%;width:256px;height:256px;margin-top:-128px;margin-left:-128px}#loadingOverlay p{position:absolute;bottom:20vh;color:white;width:100vw;text-align:center;font-size:3em}
